import React, { Component } from "react";
import { BrowserRouter, Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";

import AsyncComponent from "./hoc/AsyncComponent/AsyncComponent";
import * as actions from "./store/actions/index";
import Layout from "./hoc/Layout/Layout";
import BurgerBuilder from "./containers/BurgerBuilder/BurgerBuilder";

const AsyncCheckout = AsyncComponent(() => {
  return import("./containers/Checkout/Checkout");
});
const AsyncMyOrders = AsyncComponent(() => {
  return import("./containers/MyOrders/MyOrders");
});
const AsyncAuth = AsyncComponent(() => {
  return import("./containers/Auth/Auth");
});
const AsyncLogout = AsyncComponent(() => {
  return import("./containers/Auth/Logout/Logout");
});

class App extends Component {
  componentDidMount() {
    this.props.onCheckingAuth();
  }

  render() {
    let routes = this.props.isAuthenticated ? (
      <div>
        <Route path="/" exact component={BurgerBuilder} />
        <Route path="/auth" component={AsyncAuth} />
        <Route path="/checkout" component={AsyncCheckout} />
        <Route path="/my-orders" component={AsyncMyOrders} />
        <Route path="/logout" component={AsyncLogout} />
        <Redirect to="/" />
      </div>
    ) : (
      <div>
        <Route path="/" exact component={BurgerBuilder} />
        <Route path="/auth" component={AsyncAuth} />
        <Redirect to="/" />
      </div>
    );
    return (
      <BrowserRouter>
        <div>
          <Layout>{routes}</Layout>
        </div>
      </BrowserRouter>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.auth.token !== null,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onCheckingAuth: () => dispatch(actions.checkingAuthState()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);



