import * as actionTypes from "./actionTypes";
import axios from "../../axios-orders";

// BURGER BUILDER-CONTACT DATA -- Making and uploading orders.
export const purchaseSuccess = (id, orderData) => {
  return {
    type: actionTypes.PURCHASE_SUCCESS,
    orderId: id,
    orderData: orderData,
  };
};

export const purchaseFailure = (error) => {
  return {
    type: actionTypes.PURCHASE_FAILURE,
    value: error,
  };
};

export const purchaseBurgerStart = () => {
  return {
    type: actionTypes.PURCHASE_BURGER_START,
  };
};

export const redirect = () => {
  return {
    type: actionTypes.REDIRECT,
  };
};

export const purchaseBurger = (orderData, token) => {
  return async (dispatch) => {
    try {
      dispatch(purchaseBurgerStart());
      const res = await axios.post(`/order.json?auth=${token}`, orderData);
      dispatch(purchaseSuccess(res.data.name, orderData));
      dispatch(redirect());
    } catch (err) {
      dispatch(purchaseFailure(err));
    }
  };
};

// MY ORDERS -- Getting order's data.

export const myOrders = (orders) => {
  return {
    type: actionTypes.MY_ORDERS,
    value: orders,
  };
};

export const myOrdersFailure = (error) => {
  return {
    type: actionTypes.MY_ORDERS_FAILURE,
    value: error,
  };
};

export const fetchOrders = (token, userId) => {
  return async (dispatch) => {
    try {
      const res = await axios.get(
        `/order.json?auth=${token}&orderBy="userId"&equalTo="${userId}"`
      );
      const ordersArray = Object.entries(res.data).map((arr) => {
        return {
          ingredients: { ...arr[1].ingredients },
          totalPrice: arr[1].price,
          id: arr[0],
        };
      });
      dispatch(myOrders(ordersArray));
    } catch (err) {
      dispatch(myOrdersFailure(err));
    }
  };
};



