import * as actionTypes from "../actions/actionTypes";
import { updatedObject } from "../helpers/utility";

const initialState = {
  orders: [],
  loading: false,
  redirect: false,
  loadingOrders: true,
  error: null,
};

const orderReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.PURCHASE_BURGER_START:
      return updatedObject(state, { loading: true });
    case actionTypes.PURCHASE_SUCCESS:
      const newOrder = {
        ...action.orderData,
        id: action.orderId,
      };
      return updatedObject(state, {
        orders: state.orders.concat(newOrder),
        loading: false,
      });
    case actionTypes.PURCHASE_FAILURE:
      return updatedObject(state, { loading: false });
    case actionTypes.REDIRECT:
      return updatedObject(state, { redirect: true });
    case actionTypes.MY_ORDERS:
      return updatedObject(state, {
        loadingOrders: false,
        orders: action.value,
      });
    case actionTypes.MY_ORDERS_FAILURE:
      return updatedObject(state, {
        loadingOrders: false,
        error: action.value,
      });
    default:
      return state;
  }
};

export default orderReducer;



