import * as actionTypes from "./actionTypes";
import axios from "axios";
import { API_KEY, URL_SIGNIN, URL_SIGNUP } from "../../globals/globals";

export const authStart = () => {
  return {
    type: actionTypes.AUTH_START,
  };
};

export const authSuccess = (authData) => {
  return {
    type: actionTypes.AUTH_SUCCESS,
    value: authData,
  };
};

export const authFailure = (error) => {
  return {
    type: actionTypes.AUTH_FAILURE,
    value: error,
  };
};

export const logout = () => {
  localStorage.removeItem("token");
  localStorage.removeItem("expirationTime");
  localStorage.removeItem("userId");
  return {
    type: actionTypes.AUTH_LOGOUT,
  };
};

export const timeOut = (expireTime) => {
  return (dispatch) => {
    setTimeout(() => {
      dispatch(logout());
    }, +expireTime * 1000);
  };
};

export const authRedirectPath = (path) => {
  return {
    type: actionTypes.AUTH_REDIRECT,
    value: path,
  };
};

export const checkingAuthState = () => {
  return (dispatch) => {
    const token = localStorage.getItem("token");
    const userId = localStorage.getItem("userId");
    const expirationTime = new Date(localStorage.getItem("expirationTime"));

    if (!token) dispatch(logout());
    if (expirationTime > new Date()) {
      dispatch(authSuccess({ token: token, userId: userId }));
      dispatch(
        timeOut((expirationTime.getTime() - new Date().getTime()) / 1000)
      );
    } else {
      dispatch(logout());
    }
  };
};

export const auth = (email, password, isSignup) => {
  return async (dispatch) => {
    try {
      dispatch(authStart());
      let url = `${URL_SIGNUP}?key=${API_KEY}`;
      const authData = {
        email: email,
        password: password,
        returnSecureToken: true,
      };
      if (!isSignup) {
        url = `${URL_SIGNIN}?key=${API_KEY}`;
      }
      const res = await axios.post(url, authData);
      const succesData = {
        token: res.data.idToken,
        userId: res.data.localId,
      };
      const expiresIn = new Date(
        new Date().getTime() + res.data.expiresIn * 1000
      ); // Expire hour
      localStorage.setItem("token", res.data.idToken);
      localStorage.setItem("expirationTime", expiresIn);
      localStorage.setItem("userId", res.data.localId);
      dispatch(authSuccess(succesData));
      dispatch(timeOut(res.data.expiresIn));
    } catch (err) {
      dispatch(authFailure(err.response.data.error));
    }
  };
};



