import { INGREDIENT_PRICES } from "../../globals/ingredientsPrices";

export const updatedObject = (oldObject, updatedProps) => {
  return {
    ...oldObject,
    ...updatedProps,
  };
};

export const addIngredient = (state, action) => {
  return {
    ...state,
    ingredients: {
      ...state.ingredients,
      [action.value]: state.ingredients[action.value] + 1,
    },
    totalPrice: state.totalPrice + INGREDIENT_PRICES[action.value],
    building: true,
  };
};

export const removeIngredient = (state, action) => {
  return {
    ...state,
    ingredients: {
      ...state.ingredients,
      [action.value]: state.ingredients[action.value] - 1,
    },
    totalPrice: state.totalPrice - INGREDIENT_PRICES[action.value],
    building: true,
  };
};

export const setIngredients = (state, action) => {
  return {
    ...state,
    ingredients: {
      salad: action.value.salad,
      bacon: action.value.bacon,
      cheese: action.value.cheese,
      meat: action.value.meat,
    },
    error: false,
    totalPrice: 4,
    building: false,
  };
};



