import axios from "../../axios-orders";
import * as actionTypes from "./actionTypes";
import { returnActionObject } from "./helpers/functions";

export const addIngredient = (ingName) => {
  return returnActionObject(actionTypes.ADD_INGREDIENT, ingName);
};

export const removeIngredient = (ingName) => {
  return returnActionObject(actionTypes.REMOVE_INGREDIENT, ingName);
};

export const setIngredients = (ingredients) => {
  return {
    type: actionTypes.SET_INGREDIENTS,
    value: ingredients,
  };
};

export const error = () => {
  return {
    type: actionTypes.ERROR,
  };
};

export const initIngredients = () => {
  return async (dispatch) => {
    try {
      const res = await axios.get(
        "https://reactapp-myb-default-rtdb.firebaseio.com/ingredients.json"
      );
      const data = await res.data;
      dispatch(setIngredients(data));
    } catch (err) {
      dispatch(error());
    }
  };
};



